import Vue from 'vue'
import Router from 'vue-router'       //       // 
const Home = () => import('@/components/LiHun/Ad/Home') // 
const MyConsult = () => import('@/components/LiHun/Ad/MyConsult')  // 
const CustomAgreement = () => import('@/components/LiHun/Ad/CustomAgreement')  // 
const CustomQueDing = () => import('@/components/LiHun/Ad/CustomQueDing')  // 
const CustomBasic = () => import('@/components/LiHun/Ad/CustomBasic')  // 
const CustomShengCheng = () => import('@/components/LiHun/Ad/CustomShengCheng')  // 
const AgreementHelp = () => import('@/components/LiHun/Ad/AgreementHelp')  // 
const AgreementHelpCon = () => import('@/components/LiHun/Ad/AgreementHelpCon')  // 

const AgreementUser = () => import('@/components/LiHun/Ad/AgreementUser')  // 
const UserAgreement = () => import('@/components/LiHun/Ad/UserAgreement')  // 
const Knowledge = () => import('@/components/LiHun/Ad/Knowledge') // 
const KnowledgeCon = () => import('@/components/LiHun/Ad/KnowledgeCon') // 
const Pay = () => import('@/components/LiHun/Ad/Pay') // 

const Article = () => import('@/components/LiHun/houtai/Article')         // 
const ArticleUpload = () => import('@/components/LiHun/houtai/ArticleUpload')         // 
const FenLeiOperate = () => import('@/components/LiHun/houtai/FenLeiOperate')  // 

const UserList = () => import('@/components/LiHun/houtai/UserList')  // 
const UserWenShu = () => import('@/components/LiHun/houtai/UserWenShu')  // 

const UserOperate = () => import('@/components/LiHun/houtai/UserOperate')  // 
Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {path: '/', name: 'Home', component: Home},
    // 
    {path: '/MyConsult', name: 'MyConsult', component: MyConsult},
    // 
    {path: '/CustomAgreement', name: 'CustomAgreement', component: CustomAgreement},
    // 
    {path: '/CustomQueDing', name: 'CustomQueDing', component: CustomQueDing},
    // 
    {path: '/CustomBasic', name: 'CustomBasic', component: CustomBasic},
    // 
    {path: '/CustomShengCheng', name: 'CustomShengCheng', component: CustomShengCheng},

    // 
    {path: '/AgreementHelp', name: 'AgreementHelp', component: AgreementHelp},
    // 
    {path: '/AgreementHelpCon', name: 'AgreementHelpCon', component: AgreementHelpCon},
	
	// 
    {path: '/AgreementUser', name: 'AgreementUser', component: AgreementUser},
    // 
    {path: '/UserAgreement', name: 'UserAgreement', component: UserAgreement},
    // 
    {path: '/Knowledge', name: 'Knowledge', component: Knowledge},
    // 
    {path: '/KnowledgeCon', name: 'KnowledgeCon', component: KnowledgeCon},
    // 
    {path: '/Pay', name: 'Pay', component: Pay},

    // 
    {path: '/Article', name: 'Article', component: Article},
    // FenLeiOperate
    {path: '/ArticleUpload', name: 'ArticleUpload', component: ArticleUpload},
    // 
    {path: '/FenLeiOperate', name: 'FenLeiOperate', component: FenLeiOperate},
    // 
    {path: '/UserList', name: 'UserList', component: UserList},
    // 
    {path: '/UserWenShu', name: 'UserWenShu', component: UserWenShu},
    // 
    {path: '/UserOperate', name: 'UserOperate', component: UserOperate}
    
  ]
})



